@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@200&display=swap);
body {
  margin: 0;
  color: white;
  background-image: url("https://images.unsplash.com/photo-1493514789931-586cb221d7a7?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1351&q=80");
  background-size: cover;
  font-family: 'JetBrains Mono', monospace !important;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

i{
  font-size: 25px !important;
  margin: 0 10px 0 10px;
}

#root{
  padding: 15px 0 50px 0;
  background: #272727f2 !important;
}

#dropdown-item-button{
  color: white !important;
  background: none !important;
}

::-webkit-scrollbar {
  display: none;
}
.repo-post{
    height: 100px;
    background: #444444;
    padding: 15px;
    width: auto;
    overflow: hidden;
}

.repo-post:hover{
    background: #686868;
}

.repo-post .h4{
    font-weight: bolder;
}

.post-container{
    height: 430px;
}

.post-container a{
    color: white;
} 

.post-container a:hover{
    color: white;
}

.btn{
    background: white !important;
    color: black !important;
    border: none !important;
    border-radius: 0px !important;
}

.btn:focus{
    box-shadow: none !important;
    outline: none !important;
}

.dropdown-menu{
    width: 182px !important;
    border: 0px !important;
    border-radius: 0px !important;
    margin-top: -2px !important;
}
